import '../../styles/aboutMe.css'
import React from "react";
import Header from "../../components/Header/header";
import SpecialFooter from "../../components/SpecialFooter/SpecialFooter";

function AboutMe() {
    return (
        <body class="colorscheme-auto">
        <main className="wrapper">
            <Header/>
            <div className="content">
                <section className="container centered">
                    <div className="options">
                        <h1>About Me</h1>
                        <ul>
                            <li>
                                <a href="/Biography">
                                    <button className="button" style={{"vertical-align": "middle"}}>
                                        <span>Biography</span>
                                    </button>
                                </a>
                            </li>

                            <li>
                                <a href="/Skills">
                                    <button className="button" style={{"vertical-align": "middle"}}>
                                        <span>Skills</span>
                                    </button>
                                </a>
                            </li>

                            <li>
                                <a href="/Timeline">
                                    <button className="button" style={{"vertical-align": "middle"}}>
                                        <span>Timeline</span>
                                    </button>
                                </a>
                            </li>
                        </ul>
                    </div>
                </section>
            </div>
            <SpecialFooter/>
        </main>
        </body>
    )
}

export default AboutMe;