import React from "react";
import Header from "../../components/Header/header";
import SpecialFooter from "../../components/SpecialFooter/SpecialFooter";

function Works() {
    return (
        <body class="colorscheme-auto">
        <main className="wrapper">
            <Header/>
            <div className="content">
                <section className="container centered">
                    <div className="options">
                        <h1>Works</h1>
                        <ul>
                            <li>
                                <a href="/University-Works">
                                    <button className="button" style={{"vertical-align": "middle"}}>
                                        <span>University</span>
                                    </button>
                                </a>
                            </li>
                            <li>
                                <a href="/Fiverr-Works">
                                    <button className="button" style={{"vertical-align": "middle"}}>
                                        <span>Fiverr</span>
                                    </button>
                                </a>
                            </li>
                        </ul>
                    </div>
                </section>
            </div>
            <SpecialFooter/>
        </main>
        </body>
    )
}

export default Works;