import React from "react";
import '../../styles/header.css';
import cv from '../../files/teste.txt'
import {
    Routes,
    Route
} from 'react-router';
import Home from '../../containers/Home/home';
import AboutMe from "../../containers/AboutMe/AboutMe";
import Works from "../../containers/Works/works";

function Header() {
    return (
        <nav className="navigation">
            <section className="container">
                <Routes>
                    <Route path='/website/home' element={<Home/>}/>
                    <Route path='/website/about-Me' element={<AboutMe/>}/>
                    <Route path='/website/works' element={<Works/>}/>
                </Routes>
                <a className="navigation-title" href="/website/home">GJORDAO</a>
                <input type="checkbox" id="menu-toggle"/>
                <label className="menu-button float-right" htmlFor="menu-toggle">
                    <i className="fa fa-bars fa-fw" aria-hidden="true"/>
                </label>
                <ul className="navigation-list">
                    <li className="navigation-item">
                        <a className="navigation-link" href="/website/about-me">About Me</a>
                    </li>
                    <li className="navigation-item">
                        <a className="navigation-link" href="/website/works">Works</a>
                    </li>
                    <li className="navigation-item">
                        <a href="mailto:grtjordao782001@gmail.com" className="navigation-link">Contact me</a>
                    </li>
                    <li className="navigation-item">
                        <a download href={cv} className="navigation-link">Download CV</a>
                    </li>
                </ul>
            </section>
        </nav>
    )
}

export default Header;